.traffic-lighter {
  width: 80px;
  border: 1px solid grey; }
  .traffic-lighter__light {
    border-radius: 50%;
    margin: 0 auto;
    width: 50px;
    height: 50px; }
    .traffic-lighter__light_red.active {
      background-color: red; }
    .traffic-lighter__light_yellow.active {
      background-color: yellow; }
    .traffic-lighter__light_green.active {
      background-color: green; }

.footer {
  position: absolute;
  bottom: 0;
  width: 500px;
  height: 160px;
  border: 1px solid green; }
